
.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.tooltipped{position:relative
}
.tooltipped::after{position:absolute;z-index:1000000;display:none;padding:.5em .75em;font:normal normal 11px/1.5 -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";-webkit-font-smoothing:subpixel-antialiased;color:#fff;text-align:center;text-decoration:none;text-shadow:none;text-transform:none;letter-spacing:normal;word-wrap:break-word;white-space:pre;pointer-events:none;content:attr(aria-label);background:#1b1f23;border-radius:3px;opacity:0
}
.tooltipped::before{position:absolute;z-index:1000001;display:none;width:0;height:0;color:#1b1f23;pointer-events:none;content:"";border:6px solid transparent;opacity:0
}
@-webkit-keyframes tooltip-appear{
from{opacity:0
}
to{opacity:1
}
}
@keyframes tooltip-appear{
from{opacity:0
}
to{opacity:1
}
}
.tooltipped:hover::before,.tooltipped:hover::after,.tooltipped:active::before,.tooltipped:active::after,.tooltipped:focus::before,.tooltipped:focus::after{display:inline-block;text-decoration:none;-webkit-animation-name:tooltip-appear;animation-name:tooltip-appear;-webkit-animation-duration:.1s;animation-duration:.1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-timing-function:ease-in;animation-timing-function:ease-in;-webkit-animation-delay:.4s;animation-delay:.4s
}
.tooltipped-no-delay:hover::before,.tooltipped-no-delay:hover::after,.tooltipped-no-delay:active::before,.tooltipped-no-delay:active::after,.tooltipped-no-delay:focus::before,.tooltipped-no-delay:focus::after{-webkit-animation-delay:0s;animation-delay:0s
}
.tooltipped-multiline:hover::after,.tooltipped-multiline:active::after,.tooltipped-multiline:focus::after{display:table-cell
}
.tooltipped-s::after,.tooltipped-se::after,.tooltipped-sw::after{top:100%;right:50%;margin-top:6px
}
.tooltipped-s::before,.tooltipped-se::before,.tooltipped-sw::before{top:auto;right:50%;bottom:-7px;margin-right:-6px;border-bottom-color:#1b1f23
}
.tooltipped-se::after{right:auto;left:50%;margin-left:-16px
}
.tooltipped-sw::after{margin-right:-16px
}
.tooltipped-n::after,.tooltipped-ne::after,.tooltipped-nw::after{right:50%;bottom:100%;margin-bottom:6px
}
.tooltipped-n::before,.tooltipped-ne::before,.tooltipped-nw::before{top:-7px;right:50%;bottom:auto;margin-right:-6px;border-top-color:#1b1f23
}
.tooltipped-ne::after{right:auto;left:50%;margin-left:-16px
}
.tooltipped-nw::after{margin-right:-16px
}
.tooltipped-s::after,.tooltipped-n::after{-webkit-transform:translateX(50%);transform:translateX(50%)
}
.tooltipped-w::after{right:100%;bottom:50%;margin-right:6px;-webkit-transform:translateY(50%);transform:translateY(50%)
}
.tooltipped-w::before{top:50%;bottom:50%;left:-7px;margin-top:-6px;border-left-color:#1b1f23
}
.tooltipped-e::after{bottom:50%;left:100%;margin-left:6px;-webkit-transform:translateY(50%);transform:translateY(50%)
}
.tooltipped-e::before{top:50%;right:-7px;bottom:50%;margin-top:-6px;border-right-color:#1b1f23
}
.tooltipped-align-right-1::after,.tooltipped-align-right-2::after{right:0;margin-right:0
}
.tooltipped-align-right-1::before{right:10px
}
.tooltipped-align-right-2::before{right:15px
}
.tooltipped-align-left-1::after,.tooltipped-align-left-2::after{left:0;margin-left:0
}
.tooltipped-align-left-1::before{left:5px
}
.tooltipped-align-left-2::before{left:10px
}
.tooltipped-multiline::after{width:-webkit-max-content;width:-moz-max-content;width:max-content;max-width:250px;word-wrap:break-word;white-space:pre-line;border-collapse:separate
}
.tooltipped-multiline.tooltipped-s::after,.tooltipped-multiline.tooltipped-n::after{right:auto;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)
}
.tooltipped-multiline.tooltipped-w::after,.tooltipped-multiline.tooltipped-e::after{right:100%
}
@media screen and (min-width: 0\0){
.tooltipped-multiline::after{width:250px
}
}
.tooltipped-sticky::before,.tooltipped-sticky::after{display:inline-block
}
.tooltipped-sticky.tooltipped-multiline::after{display:table-cell
}
body {
  background-color: #F1F1F1;
  color: #555;
  font-family: "Arvo";
}
input {
  outline: !important none;
}
a {
  text-decoration: none;
  color: inherit;
  outline: none;
}
.main {
  position: relative;
  max-width: 720px;
  margin: 0 auto;
}
.main.mobile {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100%;
  background: #FFF;
}
ul.checklist {
  display: block;
  width: 100%;
  padding: 0 30px;
  background-color: #FFF;
  border-radius: 2px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-shadow: 0px 2px 1px 0px #DDD;
          box-shadow: 0px 2px 1px 0px #DDD;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.main.mobile ul.checklist {
  -webkit-box-shadow: 0px 0px 0px 0px transparent;
          box-shadow: 0px 0px 0px 0px transparent;
}
ul.checklist > li {
  position: relative;
  display: block;
  padding: 18px 0;
  color: #555;
  border-bottom: 1px solid #EEE;
  font-size: 20px;
  line-height: 26px;
  cursor: pointer;
}
.message {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
ul.checklist > li.checked {
  color: #CCC;
  text-decoration: line-through;
}
ul.checklist > li > input {
  width: 100%;
  color: #555;
  background-color: transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  font-size: inherit;
  font-family: "Arvo";
  text-align: center;
}
::-webkit-input-placeholder {
  font-style: italic;
  color: #CCC;
}
::-moz-placeholder {
  font-style: italic;
  color: #CCC;
}
::-moz-placeholder {
  font-style: italic;
  color: #CCC;
}
::-ms-input-placeholder {
  font-style: italic;
  color: #CCC;
}
ul.checklist > li:hover .item-actions {
  visibility: visible;
}
.button {
  cursor: pointer;
}
.main.mobile .item-actions {
  display: none !important;
  visibility: hidden;
}
.item-actions {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 4px;
  color: #CCC;
  font-size: 10px;
  width: 100px;
  height: 100%;
  visibility: hidden;
}
.item-actions > .button:hover {
    color: #555;
}
.item-actions > .button > svg {
    height: 100%;
}
.item-actions > .button > svg.trash {
    width: 30px;
    padding-bottom: 2px;
}
.item-actions > .button > svg.edit {
    width: 42px;
    padding-top: 5px;
}
.checklist-actions {
  height: 75px;
  padding: 20px;
  text-align: center;
  color: #E1E1E1;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.checklist-actions > .button:hover {
    color: #CCC;
}
.checklist-actions > .button > svg {
    height: 100%;
}
.checklist-actions > .button > svg.clear {
    width: 50px;
}
.checklist-actions > .button > svg.share {
    width: 50px;
}
.modal {
  width: 65%;
  margin: 0 auto;
}
.modal > input {
  width: 100%;
  margin: 30% auto;
  padding: 20px;
  color: #555;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #CCC;
  font-family: "Arvo";
  font-size: 30px;
  text-align: center;
}
.modal-cover {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(240, 240, 240, 0.8);
}
button.button {
  color: white;
  background-color: #00bcd4;
  border-color: #00bcd4;
}
span.button.mobile {
  position: fixed;
  bottom: 30px;
}
span.button.desktop {
  display: none;
}
